.swatch {
	position: relative;
	z-index: 1;

	display: flex;
	align-items: center;
	justify-content: center;

	&.-reverse {
		color: #fff;
	}

	&.-mini {
		font-size: 80%;
	}

	p {
		margin: 0;
		overflow-wrap: break-word;
		word-wrap: break-word;
	}
}

.swatch__info {
	position: relative;
	z-index: 2;
	width: 100%;

	padding: 0.5em;
	
	text-align: center;
}

.swatch__bg {
	position: absolute;
	top: 0; left: 0;
	
	width: 100%;
	height: 100%;	
	
	.swatch.-highlight & {
		background-image: linear-gradient(135deg, black, black .5em, transparent .5em, transparent);
	}

	.swatch.-reverse.-highlight & {
		background-image: linear-gradient(135deg, white, white .5em, transparent .5em, transparent);
	}
}

.swatch__contrast {
	&:before {
		content: "";
		display: inline-block;
		width: .75em;
		height: .75em;
		border-radius: 1em;
		margin-right: .25em;
	}

	&.-white {
		&:before {
			background-color: #fff;
		}
	}

	&.-black {
		&:before {
			background-color: #000;
		}
	}
}